import React from 'react';
import { Link } from 'react-router-dom';

class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false, error: null };
  }

  static getDerivedStateFromError(error) {
    return { hasError: true, error };
  }

  render() {
    if (this.state.hasError) {
      return (
        <div className="min-h-screen flex items-center justify-center bg-light p-4">
          <div className="bg-white rounded-lg shadow-md p-6 max-w-md w-full">
            <h2 className="text-2xl font-bold text-red-600 mb-4">
              죄송합니다. 오류가 발생했습니다.
            </h2>
            <p className="text-gray-600 mb-6">
              페이지를 불러오는 중에 문제가 발생했습니다. 
              다시 시도해주시거나 홈으로 돌아가주세요.
            </p>
            <div className="space-x-4">
              <button 
                onClick={() => window.location.reload()}
                className="bg-primary hover:bg-accent text-white px-4 py-2 rounded-lg transition-colors"
              >
                다시 시도
              </button>
              <Link 
                to="/"
                className="bg-gray-500 hover:bg-gray-600 text-white px-4 py-2 rounded-lg transition-colors inline-block"
              >
                홈으로 가기
              </Link>
            </div>
          </div>
        </div>
      );
    }

    return this.props.children;
  }
}

export default ErrorBoundary;