import React, { Suspense } from 'react';
import { 
 HashRouter as Router,
 Routes,
 Route,
 Link
} from 'react-router-dom';
import { motion } from 'framer-motion';
import LoadingSpinner from './components/common/LoadingSpinner';
import ErrorBoundary from './components/common/ErrorBoundary';
import { getLatestNotice } from './data/notices';

// lazy 로딩으로 컴포넌트 불러오기
const Intro = React.lazy(() => import('./components/Intro'));
const Guide = React.lazy(() => import('./components/Guide'));
const Booking = React.lazy(() => import('./components/Booking'));
const Payment = React.lazy(() => import('./components/Payment'));
const Notice = React.lazy(() => import('./components/Notice'));
const ArtSupport = React.lazy(() => import('./components/ArtSupport'));

// 메뉴 아이템 정의
const menuItems = [
 { path: '/intro', title: '시설 소개' },
 { path: '/guide', title: '이용 안내' },
 { path: '/booking', title: '회의실 예약' },
 { path: '/payment', title: '결제 안내' },
 { path: '/notice', title: '공지사항' },
 { path: '/art-support', title: '예술지원사업' }
];

const MenuItem = ({ path, title, index }) => (
 <motion.div
   initial={{ opacity: 0, y: 20 }}
   animate={{ opacity: 1, y: 0 }}
   transition={{ duration: 0.3, delay: index * 0.1 }}
   whileHover={{ scale: 1.03 }}
   whileTap={{ scale: 0.97 }}
   className="h-full"
 >
   <Link 
     to={path} 
     className="block bg-gradient-art hover:bg-gradient-hover text-white rounded-lg
                transition-all duration-300 shadow-lg hover:shadow-xl h-full
                flex flex-col items-center justify-center
                min-h-[180px] px-6 py-10
                border border-white/10 backdrop-blur-sm"
   >
     <span className="text-xl md:text-2xl font-semibold text-center">
       {title}
     </span>
   </Link>
 </motion.div>
);

function App() {
 const latestNotice = getLatestNotice();

 return (
   <Router>
     <ErrorBoundary>
       <Suspense fallback={<LoadingSpinner />}>
         <Routes>
           <Route path="/" element={
             <div className="min-h-screen bg-light flex flex-col">
               <header className="bg-gradient-art text-white p-6 shadow-lg relative overflow-hidden">
  <div className="absolute inset-0 bg-black/10 backdrop-blur-sm"></div>
  <h1 className="text-3xl md:text-4xl font-bold text-center relative z-10 title-font">
                 우리 동네 작업실, 오피스아트
                 </h1>
               </header>
               
               <main className="flex-1 container mx-auto px-4 py-8 md:py-12 max-w-7xl flex flex-col">
                 {/* 최신 공지사항 */}
                 <div className="mb-8">
                   <div className="bg-white rounded-lg shadow-md p-4 hover:shadow-lg transition-shadow">
                     <div className="flex justify-between items-center mb-2">
                       <h3 className="text-lg font-semibold text-accent">공지사항</h3>
                       <Link to="/notice" className="text-sm text-gray-500 hover:text-primary">
                         더보기 →
                       </Link>
                     </div>
                     <Link to="/notice" className="block">
                       <div className="text-gray-800 font-medium">{latestNotice.title}</div>
                       <div className="text-sm text-gray-500 mt-1">{latestNotice.date}</div>
                     </Link>
                   </div>
                 </div>

                 <motion.div 
                   className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 md:gap-8 auto-rows-fr"
                   initial={{ opacity: 0, y: 20 }}
                   animate={{ opacity: 1, y: 0 }}
                   transition={{ duration: 0.5 }}
                 >
                   {menuItems.map((item, index) => (
                     <MenuItem key={item.path} {...item} index={index} />
                   ))}
                 </motion.div>
               </main>

               <footer className="bg-gradient-art text-white py-4 mt-auto relative overflow-hidden">
                 <div className="absolute inset-0 bg-black/10 backdrop-blur-sm"></div>
                 <div className="container mx-auto px-4 text-center relative z-10">
                   <p className="text-sm md:text-base">
                     © 2024 오피스아트. All rights reserved.
                   </p>
                 </div>
               </footer>
             </div>
           } />
           <Route path="/intro" element={<Intro />} />
           <Route path="/guide" element={<Guide />} />
           <Route path="/booking" element={<Booking />} />
           <Route path="/payment" element={<Payment />} />
           <Route path="/notice" element={<Notice />} />
           <Route path="/art-support" element={<ArtSupport />} />
         </Routes>
       </Suspense>
     </ErrorBoundary>
   </Router>
 );
}

export default App;